  },
  data() {
    return {
      items: [
        {
          title: "Home",
          icon: "home",
          to: { name: "index" }
        },
        {
          title: "Inspire",
          icon: "lightbulb",
          to: { name: "inspire" }
        }
      ],
      user: {
        email: "",
        password: "",
        username: undefined,
        id: ""
      },
      notifications: undefined,
      playerSearchData: [],
      playerSelected: null,
      fetchingPlayer: false,
      playerName: "",
      selected: null,
      loading: true
    };
  },
  methods: {
    logout() {
      if (process.browser) {
        localStorage.removeItem("auth");
      }
      window.location.reload();
    },
    navUser(id) {
      this.$router.push({ path: `/user/${id}` });
    },
    getPlayerSearchResults: debounce(function(name) {
      if (!name.length) {
        this.playerSearchData = [];
        // this.playerPage = 1
        // this.totalPlayerPages = 1
        return;
      }
      this.fetchingPlayer = true;
      this.$axios(process.env.baseUrl + `api/players?name[$search]=${name}`, {
        method: "get",
        headers: {
          "content-type": "application/json"
        }
      })
        .then(function(response) {
          return response.data;
        })
        .then(response => {
          this.playerSearchData = [];
          response.data.forEach(item => this.playerSearchData.push(item));
        })
        .catch(error => {
          this.playerSearchData = [];
          throw error;
        })
        .finally(() => {
          this.fetchingPlayer = false;
        });
    }, 500),
    getPlayerImgUrl(img) {
      return process.env.baseUrl + `api/uploads/players/${img}`;
    },
    navPlayer(player) {
      this.selected = player;
      if (player) {
        this.$router.push({ path: `/players/${player._id}` });
      }
    },
    onFocus(e) {
      e.stopPropagation();
    }
  },
  components: { navBarSearch }
};
